content: center;\r\n    align-items: center;\r\n //   transition: opacity 1s ease-in-out, visibility 1s ease-in-out;\r\n\r\n}\r\n.hidden {\r\n    opacity: 0;\r\n    visibility: hidden;\r\n}\r\n.loader {\r\n    width: 120px;\r\n    height: 120px;\r\n    position: absolute;\r\n    z-index: 998;\r\n    top:50%;\r\n    left:50%;\r\n    border: 5px solid #e5e5e5;\r\n    border-top: 5px solid #ff8a03;\r\n    border-radius: 50%;\r\n    box-shadow: 0 0 6px 2px rgba(0, 0, 0, 0.2);\r\n    animation: loader 1s linear infinite;\r\n    transform: translate(-50%, -50%) rotate(0deg);\r\n  \r\n\r\n}\r\n.loader-logo {\r\n    position: absolute;\r\n    animation: 0;\r\n\r\n   margin-left: auto;\r\n   margin-right: auto;\r\n   margin-top: auto;\r\n   margin-bottom: auto;\r\n    width: 68px;\r\n    z-index: 999;\r\n\r\n}\r\n@keyframes loader {\r\n\r\n    100% {\r\n        transform: translate(-50%, -50%) rotate(360deg);\r\n    }\r\n}\r\n  // END PRELOADER\r\n\r\n.container {\r\n    @include xs {\r\n        width: 250;\r\n    }\r\n}\r\n\r\n.title {\r\n    &-underline {\r\n        padding-bottom: 2rem;\r\n        border-bottom: 1px solid var(--divider);\r\n    }\r\n}\r\n\r\n.btn {\r\n    border:0;\r\n    border-radius: 3px;\r\n    padding: 1rem 2rem;\r\n    font-weight: 700;\r\n    cursor: pointer;\r\n    transition: box-shadow 0.2s;\r\n    &:hover {\r\n        box-shadow: var(--box-shadow);\r\n        transition: box-shadow 0.2s;\r\n    }\r\n    &-primary {\r\n        background: var(--primary);\r\n        color: white;\r\n    }\r\n    &-secondary {\r\n        border:1px solid var(--primary);\r\n        background: white;\r\n        color: var(--primary);\r\n    }\r\n    &-danger {\r\n        border:1px solid var(--error);\r\n        background: white;\r\n        color: var(--error);\r\n    }\r\n}\r\n\r\n.text {\r\n    &-error {\r\n        color: var(--error);\r\n    }\r\n}\r\n\r\n.calc {\r\n    width: 100%;\r\n    position: absolute;\r\n    height: 100vh;\r\n    z-index: 0;\r\n    top: